import 'babel-polyfill';

import VueRouter from 'vue-router';
import Vue from './vue-installed';

import locales from './i18n';
import store from './store';
import routerMap from './routes';

import { i18n } from './vue-i18n';
import * as filters from 'common/js/filters';

require('./scss/jqxs_mobile.scss');
Object.keys(filters).forEach(function (k) {
  Vue.filter(k, filters[k]);
});

var transitionSlideLeftToRight = 'slide-right';
var transitionSlideRightToLeft = 'slide-left';

// document.ontouchmove = e => e.preventDefault()

// insert popstate event listener before router,
// by doing so, we can change transition name while user press "Back" button
window.addEventListener('popstate', function (_) {
  store.commit('SET_TRANSITION_NAME', transitionSlideLeftToRight);
});

var router = routerMap(VueRouter);
router.afterEach(function (route) {
  Vue.nextTick(function (_) {
    return store.commit('SET_TRANSITION_NAME', transitionSlideRightToLeft);
  });
});

var App = new Vue({
  i18n: i18n,
  router: router,
  store: store
}).$mount('#app');


